import fs from 'fs/promises';
import path from 'path';
import matter from 'gray-matter';
import { remark } from 'remark';
import html from 'remark-html';
import type { Post, LegalPage } from '@/types';

const postsDirectory = path.join(process.cwd(), 'src/content/posts');
const legalDirectory = path.join(process.cwd(), 'src/content/legal');

function calculateReadingTime(text: string): string {
  const wordsPerMinute = 200;
  const numberOfWords = text.split(/\s/g).length;
  const minutes = numberOfWords / wordsPerMinute;
  const readTime = Math.ceil(minutes);
  return `${readTime} min read`;
}

export async function getSortedPostsData(): Promise<Omit<Post, 'contentHtml' | 'readingTime' | 'slug'>[]> {
  try {
    const fileNames = await fs.readdir(postsDirectory);
    const allPostsData = await Promise.all(fileNames.map(async (fileName) => {
      const slug = fileName.replace(/\.md$/, '');
      const fullPath = path.join(postsDirectory, fileName);
      const fileContents = await fs.readFile(fullPath, 'utf8');
      const matterResult = matter(fileContents);

      return {
        slug,
        ...(matterResult.data as {
          title: string;
          date: string;
          author: string;
          description: string;
          category: string;
          tags: string[];
          image?: string;
        }),
      };
    }));

    return allPostsData.sort((a, b) => (a.date < b.date ? 1 : -1));
  } catch (error) {
    console.error("Error reading posts directory:", error);
    return [];
  }
}

export async function getAllPostSlugs() {
  try {
    const fileNames = await fs.readdir(postsDirectory);
    return fileNames.map((fileName) => {
      return {
        params: {
          slug: fileName.replace(/\.md$/, ''),
        },
      };
    });
  } catch (error) {
    console.error("Error reading post slugs:", error);
    return [];
  }
}

export async function getPostData(slug: string): Promise<Post | null> {
  try {
    const fullPath = path.join(postsDirectory, `${slug}.md`);
    const fileContents = await fs.readFile(fullPath, 'utf8');
    const matterResult = matter(fileContents);

    const processedContent = await remark()
      .use(html)
      .process(matterResult.content);
    const contentHtml = processedContent.toString();
    const readingTime = calculateReadingTime(matterResult.content);

    return {
      slug,
      contentHtml,
      readingTime,
      ...(matterResult.data as {
        title: string;
        date: string;
        author: string;
        description: string;
        category: string;
        tags: string[];
        image?: string;
      }),
    };
  } catch (error) {
    // console.error(`Error reading post data for slug ${slug}:`, error);
    return null; 
  }
}


export async function getLegalPageData(slug: string): Promise<LegalPage | null> {
  const fullPath = path.join(legalDirectory, `${slug}.md`);
  try {
    const fileContents = await fs.readFile(fullPath, 'utf8');
    const matterResult = matter(fileContents);

    const processedContent = await remark()
      .use(html)
      .process(matterResult.content);
    const contentHtml = processedContent.toString();

    return {
      slug,
      title: matterResult.data.title as string,
      contentHtml,
    };
  } catch (error) {
    // console.error(`Error reading legal page data for slug ${slug}:`, error);
    return null;
  }
}

export async function getAllLegalPageSlugs() {
  try {
    const fileNames = await fs.readdir(legalDirectory);
    return fileNames.map((fileName) => {
      return {
        params: {
          page: fileName.replace(/\.md$/, ''),
        },
      };
    });
  } catch (error) {
    console.error("Error reading legal page slugs:", error);
    return [];
  }
}

export async function getAllCategories(): Promise<string[]> {
  const posts = await getSortedPostsData();
  const categories = new Set(posts.map(post => post.category).filter(Boolean));
  return Array.from(categories);
}

export async function getAllTags(): Promise<string[]> {
  const posts = await getSortedPostsData();
  const tags = new Set(posts.flatMap(post => post.tags).filter(Boolean));
  return Array.from(tags);
}
